import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";

export default class MultiCartItemDetailComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    render() {
        return (
            <tr>
                  <td className="u-padding-l u-padding-top-l u-padding-right-l" colSpan={this.props.priceless && '3'}>
                      <span className={this.props.captionClassName}>
                        { this.props.title }
                      </span>
                  </td>
                  { !this.props.priceless &&
                      <td className="l-col-2 u-padding-l u-padding-top-l u-padding-right u-text-right">
                          <span className="u-acc-hide">Opłaty miesięczne</span>
                          { this.props.monthlyPrices && this.props.monthlyPrices[0] ?
                                this.renderPriceDesktop(this.props.monthlyPrices[0]) : this.renderVoidDesktop() }
                      </td> }
                  { !this.props.priceless &&
                      <td className="l-col-2 u-padding-l u-padding-top-l u-padding-left u-text-right">
                        <span className="u-acc-hide">Opłaty na start</span>
                        { this.props.oneTimePrice ? this.renderPriceDesktop(this.props.oneTimePrice) : this.renderVoidDesktop() }
                      </td>
                  }
            </tr>
        )
    }

}